<template><markdown-wrapper><h1 id="i18n">I18n <a class="header-anchor" href="#i18n" aria-hidden="true">&#128279;</a></h1>
<p>All uiv components use <code v-pre>en-US</code> as default language and they are both configurable.</p>
<h2 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h2>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> uiv <span class="hljs-keyword">from</span> <span class="hljs-string">'uiv'</span>
<span class="hljs-keyword">import</span> locale <span class="hljs-keyword">from</span> <span class="hljs-string">'uiv/src/locale/lang/zh-CN'</span>

Vue.use(uiv, { locale })
</code></pre>
<p>You can also create custom wordings if not satisfied with the defaults, simply create your own <code v-pre>locale</code> object and replace with the one in example code.</p>
<h2 id="with-vue-i18n">With <code v-pre>vue-i18n</code> <a class="header-anchor" href="#with-vue-i18n" aria-hidden="true">&#128279;</a></h2>
<p>uiv is compatible with <a href="https://github.com/kazupon/vue-i18n">vue-i18n</a> as well.</p>
<p><strong>Note</strong>: You need to merge uiv language packs into your app's. For example:</p>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> uivLocale <span class="hljs-keyword">from</span> <span class="hljs-string">'uiv/src/locale/lang/zh-CN'</span>

<span class="hljs-keyword">let</span> appLocale = <span class="hljs-built_in">Object</span>.assign({}, uivLocale, {
  <span class="hljs-comment">// ...</span>
})
</code></pre>
<h2 id="supported-languages">Supported languages <a class="header-anchor" href="#supported-languages" aria-hidden="true">&#128279;</a></h2>
<p>(Sorted by alphabet)</p>
<ul>
<li><strong>bg-BG</strong></li>
<li><strong>cs-CZ</strong></li>
<li><strong>de-DE</strong></li>
<li><strong>en-US</strong></li>
<li><strong>fr-FR</strong></li>
<li><strong>it-IT</strong></li>
<li><strong>nb-NO</strong></li>
<li><strong>nl-NL</strong></li>
<li><strong>pt-BR</strong></li>
<li><strong>ru-RU</strong></li>
<li><strong>zh-CN</strong></li>
</ul>
<p>Welcome to contribute if your target language is not included: just add another language config <a href="https://github.com/wxsms/uiv/blob/dev/src/locale/lang">here</a> and create a pull request.</p>
</markdown-wrapper></template><script>export default {components:{}}</script>


